import React from 'react';
import { useState, useEffect } from 'react';
import classNames from 'classnames/bind';
import styles from './AddRoomMonth.module.scss';
import { addRoomV2 } from 'api';
import { useModal } from 'component/Modal/ModalContext';
import ListRoom from '../Room';

const cx = classNames.bind(styles);

function AddRoomMonth() {
  const [data, setData] = useState({});
  const [deposit, setDeposit] = useState({}); 
  const [message, setMessage] = useState('');
  const { openModal } = useModal();

  const handleAddRoom = async () => {
      console.log(data)
      if(data.hovaten && data.SoPhong && data.GiaPhong && data.sinhhoatchung) {
        try {
            const response = await addRoomV2(data);
            if(response) {
                setData({});

                setTimeout(() => {
                  setMessage("")
                }, 3000)
                setMessage("Thm phng thnh cng!");
            }
        }catch (error) {
            setMessage(error.message);
        }
      }
      
  };

  useEffect(() => {
    const handleDeposit = (value) => {
      const rawValue = value.replace(/\./g, "");
      const numberValue = parseInt(rawValue, 10) || 0;
      return numberValue.toLocaleString();
    };
    
    ["GiaPhong", "sinhhoatchung"].forEach((key) => {
      if (data[key]) {
        const formattedValue = handleDeposit(data[key]);
        setDeposit((prev) => ({ ...prev, [key]: formattedValue }));
      }
    });    
  }, [data])


  const handleCloseBack = () => {
    openModal(<ListRoom />);
  }


  return (
    <>
        <div className={cx("container")}>
      <h2 className={cx("title")}>Thm Phng Mi</h2>
      <form className={cx("form")} id="roomForm">
        <div className={cx("formGroup")}>
          <label className={cx("label")}>H v tn ngi thu phng</label>
          <input value={data.hovaten || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, hovaten: e.target.value }))
          } type="text" id="hovaten" required className={cx("input")} />
          
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>S Phng</label>
          <input value={data.SoPhong || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, SoPhong: e.target.value }))
          } type="number" id="SoPhong" required className={cx("input")} />
        </div>


        
        <div className={cx("formGroup")}>
          <label className={cx("label")}>Gi phng thng</label>
          <input value={data.GiaPhong || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, GiaPhong: e.target.value }))
          } type="number" id="GiaPhong" required className={cx("input")} />
          <span>{deposit.GiaPhong || ''}</span>
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ph sinh hot:</label>
          <input value={data.sinhhoatchung || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, sinhhoatchung: e.target.value }))
          } type="number" id="sinhhoatchung" required className={cx("input")} />
          <span>{deposit.sinhhoatchung || ''}</span>
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ch s in c</label>
          <input value={data.chisodiencu || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, chisodiencu: e.target.value }))
          } type="number" id="chisodien" required className={cx("input")} />
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ch s nc nng c</label>
          <input value={data.chisonuocnongcu || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, chisonuocnongcu: e.target.value }))
          } type="number" id="chisonuocnongcu" required className={cx("input")} />
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ch s nc lnh c</label>
          <input value={data.chisonuoclanhcu || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, chisonuoclanhcu: e.target.value }))
          } type="number" id="chisonuoclanhcu" required className={cx("input")} />
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ch s nc nng mi</label>
          <input value={data.chisonuocnongmoi || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, chisonuocnongmoi: e.target.value }))
          } type="number" id="chisonuoclanhcu" required className={cx("input")} />
        </div>

        <div className={cx("formGroup")}>
          <label className={cx("label")}>Ch s nc lnh mi</label>
          <input value={data.chisonuoclanhmoi || ''} onChange={(e) =>
            setData((prev) => ({ ...prev, chisonuoclanhmoi: e.target.value }))
          } type="number" id="chisonuoclanhcu" required className={cx("input")} />
        </div>

        <div className={cx("control")}>
        <button type="button" onClick={() => handleCloseBack()} className={cx("button")}>Quay li</button>
        <button type="button" onClick={() => handleAddRoom()} className={cx("button")}>Thm Phng</button>
        </div>
      </form>
      <p id="message" className={cx("message")}>{message}</p>
    </div>
    </>
  );
}   


export default AddRoomMonth;