import React, { useEffect, useState } from "react";
import styles from "./PaymentInfo.module.scss";
import classNames from "classnames/bind";
import { useModal } from "component/Modal/ModalContext";
import ListRoom from "../Room";
import TotalPaymentInfo from "./TotalPaymentInfo";
import { GetUpdateRoom } from "api";
const cx = classNames.bind(styles);

function PayMentInfo({ props }) {
  const { openModal } = useModal();
  const {data, setData} = useState({})

  const HandleRefresh = async () => {
    const confirmRefresh = window.confirm(
      "Khi lm mi ha n, s liu nc mi v in mi s c chuyn thnh s liu c. Bn c chc chn mun lm mi ha n khng?"
    );
    if (!confirmRefresh) {
      alert(" hy thao tc lm mi ha n");
      return;
    }
  
    const password = prompt("Nhp mt khu  lm mi ha n");
    if (password !== "0786566176") {
      alert("Mt khu khng ng, khng th lm mi ha n");
      return;
    }
  
    let position = 0;
    
    // Dng for...of  x l async tun t
    for (const item of props) {
      position++;
  
      const object = {
        hovaten: item.hovaten,
        GiaPhong: item.GiaPhong,
        chisodiencu: item.chisodienmoi, // mi -> c
        chisodienmoi: 0,                 // reset mi
        chisonuoclanhcu: item.chisonuoclanhmoi,
        chisonuoclanhmoi: 0,
        chisonuocnongcu: item.chisonuocnongmoi,
        chisonuocnongmoi: 0,
        sinhhoatchung: item.sinhhoatchung,
        ghichu: item.ghichu,
      };
  
      const response = await GetUpdateRoom(item._id, object);
  
      if (response && position >= props.length) {
        openModal(<ListRoom />);
      }
    }
  };
  

  const handleBack = () => {
    openModal(<ListRoom />);
  };

  const Total = () => {
    const data = props
    openModal(<TotalPaymentInfo props={data} only={1} />);

  }

  return (
    <div className={cx("invoice-container")}>
    <h2 className={cx("invoice-title")}>Danh sch ha n thanh ton</h2>
    
    <div className={cx("invoice-summary")}>
      <div className={cx("summary-item")}>
        <button onClick={() => Total()} className={cx("btn-summary", "btn-total")}>
          Tng kt
        </button>
      </div>
      <div className={cx("summary-item")}>
        <button onClick={() => HandleRefresh()} className={cx("btn-summary", "btn-refresh")}>
          Lm mi
        </button>
      </div>
      <div className={cx("summary-item")}>
        <button onClick={handleBack} className={cx("btn-summary", "btn-back")}>
          Quay li
        </button>
      </div>
    </div>

  

      <div className={cx("invoice-list")}>
        {props.filter((item) => item.GiaPhong > 0).map((item) => {
          const dientieuthu = item.chisodienmoi - item.chisodiencu;
          const nuoctieuthu =
            item.chisonuoclanhmoi -
            item.chisonuoclanhcu +
            item.chisonuocnongmoi -
            item.chisonuocnongcu;
          const coastNuoc = nuoctieuthu > 0 ? nuoctieuthu * 20000 : 0;
          const coastDien = dientieuthu > 0 ? dientieuthu * 4000 : 0;
          const total =
            coastNuoc + coastDien + item.sinhhoatchung + item.GiaPhong;
          return (
            <div className={cx("invoice-card")}>
              <h3 className={cx("customer-name")}>
                {item.hovaten} - {item.SoPhong}
              </h3>
              <div className={cx("invoice-info")}>
                <span className={cx("divider")}>-----------------------------------------------</span>
                <div className={cx("invoice-info-item")}>
                  <div className={cx("info-line")}>
                    in mi: {item.chisodienmoi} kWh
                  </div>
                  <div className={cx("info-line")}>
                    in c: {item.chisodiencu} kWh
                  </div>
                  
                </div>
                <span className={cx("divider")}>--------------------------------------------------</span>
                <div className={cx("invoice-info-item")}>
                  <div className={cx("info-line")}>
                    Nc lnh mi: {item.chisonuoclanhmoi} m
                  </div>
                  <div className={cx("info-line")}>
                    Nc lnh c: {item.chisonuoclanhcu} m
                  </div>
                </div>
                <div className={cx("invoice-info-item")}>
                  <div className={cx("info-line")}>
                    Nc nng mi: {item.chisonuocnongmoi} m
                  </div>
                  <div className={cx("info-line")}>
                    Nc nng c: {item.chisonuocnongcu} m
                  </div>
                </div>
                <div className={cx("invoice-info-item")}>
                <div className={cx("info-line")}>
                    Tng nc mi: {item.chisonuoclanhmoi + item.chisonuocnongmoi} m
                  </div>
                  <div className={cx("info-line")}>
                    Tng nc c: {item.chisonuoclanhcu + item.chisonuocnongcu} m
                  </div>
                  
                </div>
              
                <span className={cx("divider")}>--------------------------------------------------</span>
                <div className={cx("invoice-info-item")}>
                 <div className={cx("info-line")}>
                    Gi phng: {item.GiaPhong.toLocaleString()}
                  </div>
                  <div className={cx("info-line")}>
                    Ph Sinh hot: {item.sinhhoatchung.toLocaleString()}
                  </div>
                </div>
                <div className={cx("invoice-info-item")}>
                  <div className={cx("info-line")}>
                    in tiu th: {dientieuthu >= 0 ? dientieuthu : 0} kWh
                  </div>
                  <div className={cx("info-line")}>
                    Nc tiu th: {nuoctieuthu >= 0 ? nuoctieuthu : 0} m
                  </div>
                </div>
                <span className={cx("divider")}>--------------------------------------------------</span>
                <div className={cx("invoice-info-item")}>
                  <div className={cx("info-line")}>
                    Tin in: {coastDien.toLocaleString()}<br></br>Gi in: {" "}
                    {dientieuthu >= 0 ? `${dientieuthu} x 4.000` : '0'}
                  </div>
                  <div className={cx("info-line")}>
                    Tin nc: {coastNuoc.toLocaleString()}<br></br>Gi
                    nc: {nuoctieuthu >= 0 ? `${nuoctieuthu} x 20.000` : '0'}
                  </div>
                </div>
                <span className={cx("divider")}>--------------------------------------------------</span>
                <span className={cx("total-amount")}>Tng thanh ton: {total.toLocaleString()}</span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default PayMentInfo;
