import React, { useEffect, useState } from "react";
import styles from "./ListRoomDetail.module.scss";
import classNames from "classnames/bind";
import { fetchRoomsList } from "../../../api";
import { editRoom } from "api";
import { useModal } from 'component/Modal/ModalContext';
import AddRoom from "../AddRoom/AddRoom";
import HistoryRoom from "../History/HistoryRoom";
import Telegram from "component/Telegram/Telegram";

const cx = classNames.bind(styles);

function ListRoomDetail() {

  const {openModal} = useModal();

  const [ListRoom, setListroom] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 5;

  useEffect(() => {
    const loadHistory = async () => {
      const getRooms = await fetchRoomsList();
      const sortedRooms = getRooms.sort((a, b) => a.roomNumber - b.roomNumber);
      setListroom(sortedRooms);
    };

    loadHistory();
  }, []);
  

  // Tnh ton d liu cho mi trang
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = ListRoom.slice(indexOfFirstItem, indexOfLastItem);

  // Chuyn sang trang k tip
  const nextPage = () => {
    if (currentPage < totalPages()) {
      setCurrentPage(currentPage + 1);
    }
  };

  // Chuyn sang trang trc
  const prevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  // Tnh tng s trang
  const totalPages = () => {
    return Math.ceil(ListRoom.length / itemsPerPage);
  };


//   const handleDeleteItem = async (idToDelete) => {
//     try {
//       // Gi yu cu xa n backend
//       const response = await fetchHistory(idToDelete);

//       // Kim tra nu xa thnh cng (ty vo response t backend)
//       if (response.status === 200) {
//         // Xa phn t khi state history
//         const updatedHistory = history.filter(item => item._id !== idToDelete);
//         setHistory(updatedHistory);
//       } else {
//         console.error("Khng th xa lch s, vui lng th li.");
//       }
//     } catch (error) {
//       console.error("Li khi xa lch s:", error);
//     }
//   };

const handleEditRoom = async(roomID) => {
    const response = await editRoom(roomID, {deleted: true});
    if(response) {
      window.location.reload();
    }    
  }

const handleOpenAddRoom = () => {
    openModal(<AddRoom />);
}

const handleHistoryRoom = () => {
    openModal(<HistoryRoom />);
}

const handleOpenTelegram = () => {
  openModal(<Telegram props={2} />);
}


  return (
    <div>
      <h1 className={cx("title-table")}>Danh sch phng c th</h1>
      <div className={cx("control")}>
        <button onClick={(e) => handleOpenAddRoom()} className={cx("add-room")}>Thm phng</button>
        <button onClick={(e) => handleOpenTelegram()} className={cx("telegram")}>iu khin bot qua telegram</button>
      </div>
      <table className={cx("history-table")}>
     
        <thead>
          <tr>
            <th>S Phng</th>
            <th>Gi phng</th>
            <th>Lch s thu</th>
            <th>Hnh ng</th>
            
          </tr>
        </thead>
        <tbody className={cx("data")}>
          {currentItems && currentItems.length > 0 ? (
            currentItems.map((item, index) => (
              <tr key={index}>
                <td>Phng: {String(item.roomNumber).padStart(3, "0")}</td>
                <td>
                    <span>Gi phng ngy: {item.daily.toLocaleString()} VN</span>
                    <span>Gi phng gi u: {item.firstHour.toLocaleString()} VN</span>
                    <span> Gi phng gi u: {item.extraHour.toLocaleString()} VN</span>
                    
                   
                </td>
                <td><button onClick={(e) => handleHistoryRoom()}>Xem ngay</button></td>
                <td><button onClick={(e) => handleEditRoom(item._id)}>Xa</button></td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="6" style={{ textAlign: 'center', padding: '20px', fontStyle: 'italic' }}>
                Khng c bng no c ghi
              </td>
            </tr>
          )}

        </tbody>
      </table>

      {/* Pagination Controls */}
      <div className={cx("pagination")}>
        <div className={cx("notification")}><span>Trang {currentPage} of {totalPages()}</span></div>
        <div className={cx("controls")}>
          <button onClick={prevPage} disabled={currentPage === 1}>
            Trang trc
          </button>
          <button onClick={nextPage} disabled={currentPage === totalPages()}>
            Trang sau
          </button>
        </div>
      </div>
    </div>
  );
}

export default ListRoomDetail;
